import { isArray, isString, isObject } from 'lodash'

function isVnode (vnode) {
  return isObject(vnode) && vnode.componentOptions
}

/**
 * refDOM
 * 1refcontextrefDOM
 * 2refref.$el
 * 3refref
 * 4null
 *
 * @param {string|VueComponent|Node} ref 
 * @param {VueContext=} context ref
 * @return {Node}
 */
export function getNodes (ref, context) {
  if (!ref) {
    return []
  }

  let vnodes = getVnodes(ref, context)
  return vnodes.map(item => {
    if (isVnode(item)) {
      return item.elm
    }
    return item
  })
}

export function getVnodes (ref, context) {
  if (!ref) {
    return []
  }
  let vnodes
  if (isString(ref)) {
    vnodes = context.$refs[ref]
    if (!vnodes) {
      vnodes = []
    } else {
      vnodes = isArray(vnodes) ? vnodes : [vnodes]
    }
    vnodes = vnodes.map(item => item.$vnode || item)
  } else {
    ref = isArray(ref) ? ref : [ref]
    vnodes = ref.map(item => {
      if (item.$vnode) {
        return item.$vnode
      } else if (isVnode(item) ||
        item.nodeType === 1 ||
        item.nodeType === 3) {
        // vnodedom
        return item
      }
    })
  }
  return vnodes || []
}



