<template>
  <div class="demo-special-dialog">
    <article>
      <h1><code>&lt;veui-special-dialog&gt;</code></h1>
      <section>
        <veui-button @click="popupAlerts"> AlertBox</veui-button>
      </section>
      <section>
        <veui-button @click="popupConfirms"> ConfirmBox</veui-button>
      </section>
      <section>
        <veui-button @click="popupToasts"> toasts</veui-button>
      </section>
      <section>
        <veui-button @click="popupPrompt">prompt</veui-button>
      </section>
    </article>
  </div>
</template>

<script>
import Button from '@/components/Button'
import alertManager from '@/managers/alert'
import confirmManager from '@/managers/confirm'
import promptManager from '@/managers/prompt'
import toastManager from '@/managers/toast'

export default {
  components: {
    'veui-button': Button
  },
  data () {
    return {
      alerts: [],
      confirms: [],
      toasts: []
    }
  },
  methods: {
    popupAlerts () {
      alertManager.success('', '', {
        ok () {
          alert('')
        }
      })
      alertManager.info('', '')
      alertManager.error('', '')
    },
    popupConfirms () {
      confirmManager.popup('', '', {
        ok () {
          alert('')
        }
      })
    },
    popupToasts () {
      let counter = 1
      setInterval(() => {
        counter++
        const type = ['error', 'info', 'success'][counter % 3]
        toastManager[type](`${type}-${counter}`)
      }, 1000)
    },
    popupPrompt () {
      promptManager.popup('content', 'title', {
        content: 'content',
        title: 'title',
        ok (value) {
          alert(value)
        }
      })
    }
  }
}
</script>

<style lang="less">
</style>



