<template>
  <article class="demo-dialog">
    <h1><code>&lt;veui-dialog&gt;</code></h1>
     <p>
      <veui-dialog :modal="true"
        overlay-class="test"
        :open="modalDialogVisible"
        @update:open="(value) => modalDialogVisible = value"></veui-dialog>
      <veui-button ui="primary"
        @click="modalDialogVisible = !modalDialogVisible"></veui-button>

      <veui-dialog :modal="false"
        :open="nonModalDialogVisible"
        @update:open="(value) => nonModalDialogVisible = value"></veui-dialog>
      <veui-button ui="primary"
        @click="nonModalDialogVisible = !nonModalDialogVisible"></veui-button>
    </p>
    <p>
      <veui-dialog :draggable="true"
        :modal="false"
        :open="draggableDialog1Visible"
        @update:open="(value) => draggableDialog1Visible = value"
        ui="center"
        title=""></veui-dialog>
      <veui-button ui="primary" @click="draggableDialog1Visible = !draggableDialog1Visible"></veui-button>

       <veui-dialog :draggable="true"
        :modal="false"
        :open="draggableDialog2Visible"
        title=""
        @update:open="(value) => draggableDialog2Visible = value"
        ui="reverse top"></veui-dialog>
      <veui-button ui="primary" @click="draggableDialog2Visible = !draggableDialog2Visible"></veui-button>
    </p>
     <p>
      <veui-dialog :open="operationDialogVisible"
        @update:open="(value) => operationDialogVisible = value"
        @ok="handleOk"
        @cancel="handleCancel"></veui-dialog>
      <veui-button ui="primary" @click="operationDialogVisible = true"></veui-button>
    </p>
    <p>
      <veui-dialog :open="customTextTitleDialogVisible"
        @update:open="(value) => customTextTitleDialogVisible = value"
        :title="''"></veui-dialog>
      <veui-button ui="primary" @click="customTextTitleDialogVisible = true"></veui-button>

      <veui-dialog :open="customIconTitleDialogVisible"
        @update:open="(value) => customIconTitleDialogVisible = value">
        <template slot="title">ICON <icon class="svg" name="calendar"></icon></template>
      </veui-dialog>
      <veui-button ui="primary" @click="customIconTitleDialogVisible = true">ICON</veui-button>
    </p>

    <p>
      <veui-dialog :open="customContentDialogVisible"
        @update:open="(value) => customContentDialogVisible = value">
        <div></div>
      </veui-dialog>
      <veui-button ui="primary" @click="customContentDialogVisible = true"></veui-button>
    </p>

    <p>
      <veui-dialog :open="contentAutoHeightDialogVisible"
        @update:open="(value) => contentAutoHeightDialogVisible = value">
        <div></div>
        <div v-html="dynamicContent"></div>
      </veui-dialog>
      <veui-button ui="primary" @click="contentAutoHeightDialogVisible = true"></veui-button>
      <veui-dialog :open="veryLongDialogVisible"
        @update:open="(value) => veryLongDialogVisible = value"
        draggable
        :modal="false">
        <div class="RichContent-inner"><span class="RichText CopyrightRichText-richText"><p></p><img style="max-width:100%" src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1495178619&di=405a920d0891049815b6c2e67cf288cf&imgtype=jpg&er=1&src=http%3A%2F%2Fimg3.duitang.com%2Fuploads%2Fitem%2F201504%2F11%2F20150411H1005_Gr4PZ.jpeg" data-rawwidth="780" class="origin_image zh-lightbox-thumb" width="780"><br><br><p></p><p></p><p>5</p><p>  10</p><br><img style="max-width:100%" data-rawheight="655" src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1494585692441&di=ece6067de053e8e74c4abf1cb0d23d7e&imgtype=0&src=http%3A%2F%2Ff.hiphotos.baidu.com%2Fzhidao%2Fpic%2Fitem%2F902397dda144ad3464639dc8d1a20cf430ad85a4.jpg" data-rawwidth="545" class="origin_image zh-lightbox-thumb" width="545"><br><img style="max-width:100%" data-rawwidth="640" data-rawheight="852" src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1494585692441&di=ece6067de053e8e74c4abf1cb0d23d7e&imgtype=0&src=http%3A%2F%2Ff.hiphotos.baidu.com%2Fzhidao%2Fpic%2Fitem%2F902397dda144ad3464639dc8d1a20cf430ad85a4.jpg" class="origin_image zh-lightbox-thumb" width="640" data-original="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1494585636287&di=0911d3a6f878002561337bf8dd2bdf12&imgtype=0&src=http%3A%2F%2Fd.hiphotos.baidu.com%2Fzhidao%2Fpic%2Fitem%2F72f082025aafa40fe871b36bad64034f79f019d4.jpg"></span></div>
      </veui-dialog>
      <veui-button ui="primary" @click="veryLongDialogVisible = true"></veui-button>
    </p>
  </article>
</template>
<script>
import Dialog from '@/components/Dialog'
import Button from '@/components/Button'

export default {
  name: 'dialog-demo',
  components: {
    'veui-dialog': Dialog,
    'veui-button': Button
  },
  data () {
    return {
      modalDialogVisible: false,
      nonModalDialogVisible: false,

      draggableDialog1Visible: false,
      draggableDialog2Visible: false,

      operationDialogVisible: false,

      customTextTitleDialogVisible: false,
      customIconTitleDialogVisible: false,

      customContentDialogVisible: false,

      contentAutoHeightDialogVisible: false,
      veryLongDialogVisible: false,

      dynamicContent: '',

      test: '123'
    }
  },
  created () {
    setInterval(() => {
      this.dynamicContent += `${Date.now()}<br>`
    }, 1000)
  },
  watch: {
    contentAutoHeightDialogVisible (value) {
      if (value) {
        this.dynamicContent = ''
      }
    }
  },
  methods: {
    handleOk () {
      alert('')
    },
    handleCancel () {
      alert('')
    }
  }
}
</script>

<style lang="less">
.demo-dialog {
  .svg {
    width: 20px;
  }

  .veui-button {
    margin-right: 10px;
  }
}
</style>



